* {
    padding: 0;
    margin: 0;
    box-sizing: border-box; }

nav {
    height: 47px;
    background: #004272;
    padding: 0 218px; }
.nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    &_item {
        position: relative; }
    &_link {
        display: block;
        font-weight: 400;
        font-size: 18px;
        padding: 12px 0 13px 0;
        line-height: 21px;
        color: #FFFFFF;
        text-decoration: none;
        transition: all .5s ease;
        &:hover {
            color: #f3d1d1; } }

    &_img_up, &_img_down {
        display: block;
        position: absolute;
        left: 27px;
        &:hover {
            color: #f3d1d1; }
