        //     display: block
        //     content: ""
        //     transform: rotate(180deg)
        //     &:hover
        //         color: blue
        // &:after
        //     display: block
        //     content: ""
 } }        //     transform: rotate(-180deg)
.nav_item:hover .dropdown_menu {
    display: block;
    position: absolute;
    top: 47px;
    background: #00C2FF;
    padding: 18px 0 22px 15px;
    width: 120px;
    &_item {}
    &_link {
        padding: 0;
        cursor: pointer; } }
.dropdown_menu {
    display: none; }

.nav_item:hover span {
    display: block;
    position: absolute;
    top: 34px;
    width: 79px;
    height: 13px;
    background: #00C2FF;
    cursor: pointer; }
.nonactive {
    display: none; }
