        padding: 1px 0 3px 0;
        margin: 0 auto;
        margin-top: 20px;
        transition: all .5s ease;
        &:hover {
            border-color: rgba(0,0,0,0.25);
            background-color: rgba(219, 225, 236, 88%);
            border-radius: 25px; } }


    &_button_prev, &_button_next {
        display: block;
        cursor: pointer;
        position: absolute;
        top: 42%;
        width: 67px;
        height: 67px;
        border: 1px solid;
        border-radius: 50%;
        transition: .5s ease;
        &:hover {
            border-color: rgba(0, 0, 0, .25);
            background-color: rgba(155,155 ,155, .25 ); } }
    &_button_prev {
        left: -85px; }
    &_button_next {
        right: -85px; }
    &_img_prev, &_img_next {
        display: block;
        width: 50px;
        height: 45px;
        /* opacity: .25; */
        margin: 0 auto;
        position: absolute;
        top: 10px;
        right: 0;
        left: 7px; }
    &_img_prev {
        right: 30px;
        left: 5px; }
    .dots {
        text-align: center;
        margin-top: 27px; }
    .dot {
        display: inline-block;
        transition: background-color .6s ease;
        cursor: pointer;
        height: 13px;
        width: 13px;
        background: #fff;
        border: 1px solid;
        border-color: rgba(0, 0, 0, .25);
        border-radius: 50%;
        margin: 0 2px; }
    .active, .dot:hover {
        border-color: rgba(155, 155, 155, .25);
        background-color: rgb(0, 0, 0);
 }        // background-color: #717171
    .carousel {
        animation: carousel 1.5s ease; }
    @keyframes carousel {
        from {
            opacity: .4; }
        to {
            opacity: 1; } } }
